import { profileAPI } from '../api/api';

const ADD_POST = "ADD-POST";
const GET_USERS_PROFILE = "GET-USERS-PROFILE";
const GET_STATUS = "GET-STATUS";

let initiate = {
    posts: [
        { id: 1, "message": 'Hello, it is first post' },
        { id: 2, "message": 'Hello, it is second post !!!!!!!!' },
    ],
    postText: '',
    profile: null,
    status: ""
};

const profileReducer = (state = initiate, action) => {

    switch (action.type) {
        case ADD_POST: {
            let postItem = { id: 3, message: action.postText };

            return {
                ...state,
                posts: [...state.posts, postItem],
                postText: ''
            }
        };




        case GET_USERS_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        };

        case GET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        };

        default: return state;
    }
}


export const addPostCreateAction = (postText) => ({ "type": ADD_POST, postText })
export const setUsersProfile = (profile) => ({ "type": GET_USERS_PROFILE, profile })
export const getStatusProfile = (status) => ({ "type": GET_STATUS, status: status })



export const getProfileThunk = (userId) => async (dispatch) => {


    if (!userId) {
        userId = 2;
    }
    let response = await profileAPI.getProfile(userId);
    dispatch(setUsersProfile(response.data))

}

export const getStatusThunk = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId);
    dispatch(getStatusProfile(response.data));

}

export const updateStatusThunk = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status);
    if (response.data.resultCode === 0) {
        dispatch(getStatusProfile(status))
    }


}




export default profileReducer


