import { authAPI } from '../api/api';
import { stopSubmit } from 'redux-form';

const SET_AUTH_USER_DATA = "SET-AUTH-USER-DATA";


let initiate = {
    userId: null,
    email: null,
    login: null,
    isAuth: false
};

const authReducer = (state = initiate, action) => {

    switch (action.type) {
        case SET_AUTH_USER_DATA: {
            return {
                ...state,
                ...action.data
            }
        };


        default: return state;
    }
}


export const setAuthUserData = (userId, email, login, isAuth) => ({ "type": SET_AUTH_USER_DATA, data: { userId, email, login, isAuth } })

export const getAuthStatus = () => (dispatch) => {

    return authAPI.getLoginAuth().then(response => {

        if (response.data.resultCode === 0) {
            let { id, login, email } = response.data.data;
            dispatch(setAuthUserData(id, email, login, true));
        }
    });

}

export const login = (email, password, rememberMe) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe);

    if (response.data.resultCode === 0) {
        dispatch(getAuthStatus());

    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "somthing wrong";
        dispatch(stopSubmit("login", { _error: message }));
    }


}



export const deleteAuthStatus = () => async (dispatch) => {
    let response = await authAPI.logout();

    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }


}




export default authReducer


