import React from "react";
import s from "./Header.module.scss";
import { NavLink } from "react-router-dom";

let Header = (props) => {


  return (

    <header >
      <a href="">
        <img src="logo.png" />
      </a>

      <div className={s.login}>

        {props.isAuth ? <div> {props.loginAuth} < span onClick={props.deleteAuthStatus} > Выход</span></div> : <NavLink to={'/login'}>Login</NavLink >}

      </div>
    </header >
  );
}

export default Header;
