
import { headerAPI } from '../api/api';
import { updateObjectInArray } from '../util/Object-helpers';


const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET-UDERS";
const SET_CURRENT = "SET-CURRENT";
const ALL_COUNT_USERS = "ALL-COUNT-USERS";
const TOGGLE_FETCH = "TOGGLE-FETCH";
const TOGGLE_FETCH_PROGRESS = "TOGGLE-FETCH-PROGRESS";


let initiate = {
    users: [

    ],
    currentPage: 1,
    pageSize: 10,
    totalUsersCount: 0,
    isFetch: true,
    followingProgress: []
};

const usersReducer = (state = initiate, action) => {

    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id", { followed: true })
            }

        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id", { followed: false })
                /* users: state.users.map(m => {
                     if (m.id === action.userId) {
                         return { ...m, followed: false }
                     }
 
                     return m;
                 }) */
            }


        case SET_USERS:
            return {
                ...state, users: action.users
            }

        case SET_CURRENT:
            return {
                ...state, currentPage: action.current
            }

        case ALL_COUNT_USERS:
            return {
                ...state, totalUsersCount: action.count
            }

        case TOGGLE_FETCH:
            return {
                ...state, isFetch: action.isFetch
            }

        case TOGGLE_FETCH_PROGRESS:
            return {
                ...state,
                followingProgress: action.isFetch ?
                    [...state.followingProgress, action.userId] :
                    [state.followingProgress.filter(id => id != action.userId)]
            }



        default: return state;
    }
}


export const follow = (userId) => ({ "type": FOLLOW, userId });
export const unfollow = (userId) => ({ "type": UNFOLLOW, userId });
export const setUsers = (users) => ({ "type": SET_USERS, users });
export const setCurrent = (current) => ({ "type": SET_CURRENT, current });
export const setAllUsersCount = (count) => ({ "type": ALL_COUNT_USERS, count });
export const setIsFetch = (isFetch) => ({ "type": TOGGLE_FETCH, isFetch });
export const togglefollowingProgress = (isFetch, userId) => ({ "type": TOGGLE_FETCH_PROGRESS, isFetch, userId });

export const getUsersThunkCreator = (currentPage, pageSize) => async (dispatch) => {


    dispatch(setIsFetch(true));
    dispatch(setCurrent(currentPage));
    let response = await headerAPI.getUsers(currentPage, pageSize)
    dispatch(setIsFetch(false));
    dispatch(setUsers(response.data.items));
    dispatch(setAllUsersCount(response.data.totalCount));

}

const followUnfollow = async (dispatch, nameMethod, userId, actionCreator) => {
    dispatch(togglefollowingProgress(true, userId));
    let data = await nameMethod(userId);

    if (data.resultCode == 0) {
        dispatch(actionCreator(userId));
    }
    dispatch(togglefollowingProgress(false, userId));;

}


export const getUnfollow = (userId) => async (dispatch) => {

    followUnfollow(dispatch, headerAPI.getfollow, userId, unfollow);


    /* dispatch(togglefollowingProgress(true, userId));
    let data = await headerAPI.getfollow(userId);

    if (data.resultCode == 0) {
        dispatch(unfollow(userId));
    }
    dispatch(togglefollowingProgress(false, userId));
*/

}

export const getFollow = (userId) => async (dispatch) => {


    followUnfollow(dispatch, headerAPI.getUnfollow, userId, follow);

    /* dispatch(togglefollowingProgress(true, userId));
     let data = await headerAPI.getUnfollow(userId);
 
     if (data.resultCode == 0) {
         dispatch(follow(userId));
     }
     dispatch(togglefollowingProgress(false, userId));;
 */

}

export default usersReducer


