import React from "react";
import Header from "./Header";
import { connect } from "react-redux";
import { getAuthStatus, deleteAuthStatus, login } from '../../Redux/auth-reducer';

class HeaderContainer extends React.Component {

  componentDidMount() {

    this.props.getAuthStatus();

  }



  render() {
    return (
      <Header {...this.props} deleteAuthStatus={this.props.deleteAuthStatus} />

    );
  }
}

let setStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
  loginAuth: state.auth.login
});
export default connect(setStateToProps, { getAuthStatus, deleteAuthStatus, login })(HeaderContainer);
