const ADD_MESSAGE = "ADD-MESSAGE";

let initiate = {
    dialogs: [
        { "id": 1, "name": "Андрей" },
        { "id": 2, "name": "Степан" },
        { "id": 3, "name": "Жена" },
        { "id": 4, "name": "Артем" },
        { "id": 5, "name": "Мама" }
    ],
    messages: [
        { "id": 1, "messages": "Привет, как дела?" },
        { "id": 2, "messages": "Ты тут?" },
        { "id": 3, "messages": "Завтра пойдем в кино?" }

    ],
    messageText: ''
};

const dialogsReducer = (state = initiate, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            let messageItem = { "id": 5, "messages": action.messageText };

            return {
                ...state,
                messages: [...state.messages, messageItem],
                messageText: ''
            }
        }

        default: return state;

    }
}



export const addMessagesCreateAction = (messageText) => ({ "type": ADD_MESSAGE, messageText })
export default dialogsReducer
