
import { getAuthStatus } from './auth-reducer';

const GET_INITIALAZET = "GET_INITIALAZET";


let initiate = {
    initialazet: false
};

const appReducer = (state = initiate, action) => {

    switch (action.type) {
        case GET_INITIALAZET: {
            return {
                ...state,
                initialazet: true
            }
        };


        default: return state;
    }
}


export const initialazeSuccess = () => ({ "type": GET_INITIALAZET })

export const initialazeApp = () => (dispatch) => {
    let promise = dispatch(getAuthStatus());
    Promise.all([promise])
        .then(() => {
            dispatch(initialazeSuccess());
        });
}

export default appReducer


