import React from "react";
import "./App.scss";
import HeaderContainer from "./Components/Header/HeaderContainer";
import Navbar from "./Components/Navbar/Navbar";
import { Route, withRouter } from "react-router-dom";
import { initialazeApp } from "./Redux/app-reducer"
import { connect } from "react-redux";
import { compose } from "redux";
import Preloader from "./Components/common/preloader/Preloader";
import {lazySuspence} from "./hoc/lazySuspense";

const DialogsContainer = React.lazy(() => import('./Components/Dialogs/DialogsContainer'))
const ProfileContainer = React.lazy(() => import('./Components/Profiles/ProfileContainer'));
const UsersContainer = React.lazy(() => import('./Components/Users/UsersContainer'));
const Login = React.lazy(() => import('./Components/Login/login'));
class App extends React.Component {

  componentDidMount() {
    this.props.initialazeApp();
  }

  render() {

    if (!this.props.initialazet) {
      return <Preloader />
    }
    return (


      <div className="app-wrapper" >
        <HeaderContainer />

        <div className="main-content">
          <Navbar />
          <div className="content">

            <Route path='/dialogs' render={() =>{return lazySuspence(DialogsContainer);}}/>
            <Route path='/profile/:userId?' render={() =>{return lazySuspence(ProfileContainer)}} />
            <Route path='/users' render={() => {return lazySuspence(UsersContainer)}} />
            <Route path='/login' render={() => {return lazySuspence(Login)}} />


          </div>
        </div>

      </div >

    );
  }
}
let mapStateToProps = (state) => ({
  initialazet: state.app.initialazet
});

export default compose(
  withRouter,
  connect(mapStateToProps, { initialazeApp })
)(App)
